			'YEARBOOK RA MẮT, BẠN CÓ TÂM ĐẮC? \n\nCuối cùng Yearbook của /một • bảy • hai • không/ đã ra đời sau bao tháng nung nấu tại https://win-of-1720.now.sh/!\n\nCó những điều và những người mà chúng ta "không muốn chôn vùi vào kí ức". Vì vậy, hãy truy cập vào web khối và tìm đến cuốn sách này, để biết những thứ tuyệt đẹp vẫn luôn ở đây.\n\nBây giờ, hãy cùng chúng mình điểm mặt gọi tên một số người bạn đã để lại những câu nói chất lừ đến không thể chối từ ngay bên dưới. Nhưng cũng đừng quên, bạn cũng có thể truy cập vào Yearbook để tìm được nhiều điều hơn thế nữa. \n---\n\nLink file Yearbook sẽ được gửi về cho đại diện các lớp, /một • bảy • hai • không/ hãy nhanh chóng liên hệ với đại diện lớp mình để nhận file in ngay nhé!\n\nVà nếu có thấy Yearbook đáng yêu quá, bạn cũng có thể hỗ trợ chúng mình theo hướng dẫn: http://tinyurl.com/HuongDanDonate\n\nCòn bây giờ, hãy đọc và cảm nhận để cùng ngồi với nhau, về chốn xưa ban đầu.',
