			"https://www.facebook.com/2034783130179068/posts/2795446380779402/",
		message:
			"17: ĐƯỢC LÊN BÁO VÀ LỜI XIN LỖI TỪ BTC 17:\n…\nLời đầu tiên, chúng mình muốn cảm ơn báo Hoa Học Trò vì đã phỏng vấn và viết về chúng mình. Dẫu biết BTC chúng mình còn nhiều sai sót nhưng nhìn chặng đường đi qua, tụi mình cảm thấy trân quý vô cùng. Một lần nữa, xin cảm ơn mọi người rất nhiều.\n\nLời thứ hai, tụi mình xin lỗi các bạn vì không thể lên yearbook đúng dự kiến. Tuy nhiên, tụi mình đang hoàn thành nhanh hết sức có thể và mong có thể ra kịp trong ngày mai.\n…\n\nVẫn còn một vài hoạt động tụi mình chưa làm xong. \n\nDẫu biết là mọi thứ đã trôi qua nhưng trước khi năm học mới bắt đầu, tụi mình muốn cất giữ cho các bạn những kí ức thanh xuân quý giá nhất.\n\nCác bạn có sẵn sàng đợi chúng mình không? \n…\n\nKhi các bạn đọc xong những dòng này, chúng mình vừa chỉnh xong (lần cuối của lần cuối)x10 của 1001 file .PNG và đợi Premiere xuất (chắc lần cuối) sau 7749 lần sửa...\n\n…\n\nMột dấu mốc nữa của thanh xuân: https://bit.ly/31vmMa3",
