			"https://www.facebook.com/2034783130179068/posts/2779091542414886/",
		message:
			'Trời xanh,\nchuyện tốt đẹp thì lại mong manh.\n\nChiều nay không có mưa bay, nhưng lòng mình lại tuôn hoài một nỗi buồn không dứt.\n\nMười hai năm ấy, đã bao lần\nmình cùng vượt qua những lần kiểm tra khó nhằn nhất, \nmình cùng nghe tiếng chuông thân thuộc reo lên mỗi ngày,\nmình cùng thở phào nhẹ nhõm khi buông bút xong bài môn cuối.\n\nNhưng hôm nay, sau bài thi cuối cùng này, cả sắc biếc xanh của trời cũng không thể khiến lòng mình ngừng mưa rơi. \nTiếng chuông reo như thường ngày vẫn vậy, sao lại có thể ngân vang đến thế?\nKhoảnh khắc mình mong chờ từ lâu, sao lại có thể buồn đến thế?\nMười hai năm, sao lại có thể nhanh đến thế?\n\nRõ ràng là đang đứng ở hiện tại, nhưng mình cứ nhung nhớ khắc khoải mười hai năm đã qua, cũng lại muôn trùng lo âu về tương lai sắp đến.\n\n"Mười hai năm", giờ đây chỉ còn là lời nói trên đầu môi.\n \nRồi mình sẽ phải đi tiếp, như một quy luật vốn dĩ, đến những tháng ngày xa lạ, những khung trời mới mẻ, với muôn vàn sự đời không đoán trước. Nhưng mỗi khi chùn chân mỏi gối, chỉ cần quay đầu lại sẽ thấy, Nhà vẫn luôn ở đây.\n\n"Có biết đâu 100 ngày sau đời lại hóa màu xanh?"\nCó biết đâu 100 ngày sau, lòng mình lại hoá màu xanh.',
