		permalink_url:
			"https://www.facebook.com/2034783130179068/posts/2776730429317664/",
		message:
			'NẾU NGÀY MAI KHÔNG ĐẾN\nNếu ngày mai mình đau bụng\nNếu ngày mai mình sẩy tay\nNếu ngày mai có gì bất trắc... \n\nGiờ phút này, khi đứng ở sau vạch xuất phát mà trước mắt là đường đua quan trọng nhất của thanh xuân, hàng vạn câu "Nếu như..." cứ thế chạy mãi chạy mãi trong đầu chúng ta. Chúng ta lo vì những thứ nhỏ nhất, cho đến những thứ lớn nhất. Chúng ta sợ bất cứ điều gì có thể xảy ra ngoài tầm với.\n\nNhưng mà bạn ơi,\n18 tuổi và 12 năm cắp sách tới trường, đã có khó khăn nào mà chúng ta chưa từng vượt qua?\n\nBước vào một vòng thi quan trọng, ai cũng trong trạng thái tim đập, chân run. Mỗi lúc như vậy, xin hãy nhớ:\nbạn đã có lần làm bài không như mong đợi,\ncũng đã từng bước vào thi với cái đầu trống,\nvà cũng có khi đau bụng, nhức đầu.  \n\nNhưng, chính bạn cũng đã từng vượt qua tất cả. \n\nDù có chuyện gì đi nữa, ngày mai vẫn sẽ đến, chúng ta sẽ đi đến chặng cuối của hành trình. Vì vậy, xin hãy ghi nhớ vài điều:\ndù bạn đi xa bao nhiêu thì những gì bạn học được trong suốt hành trình mới là thứ quan trọng hơn đích đến,\nvà vì bạn là người đã trải qua những điều khó khăn, ngày hôm nay bạn không có gì phải sợ.\n\nĐừng để những âu lo - thứ chỉ đơn thuần là cảm giác, che mất đi cái tôi kiên cường đã được dung dưỡng bấy lâu từ sâu bên trong. Hãy tỉnh táo và mạnh mẽ, như chính bản thân bạn của mọi lần. \n\nBởi,\nbạn là một người phi thường,\nlà chính bạn, và là /một bảy hai không/. \n\n---\nGiờ đây, hãy chuẩn bị đầy đủ các loại bút, thước, tẩy, máy tính, giấy báo thi, căn cước và đồng hồ. Sau đó lên giường và ngủ một giấc thật sâu chờ ngày mai đến.\n\n17: /một bảy hai không/ chúc bạn sẽ bước vào phòng thi với tất cả sự tự tin, và bước ra với tất cả sự hào.\nBạn, mình và chúng ta,\ntất cả hãy cùng thi thật tốt! \n\nTrân trọng,\n17: /một bảy hai không/',
