					description:
						"CHỈ CÒN 9 NGÀY\n\nBạn đã nhận giấy báo thi của mình chưa?\n\nVậy là chỉ còn 9 ngày nữa thôi, /một bảy hai không/ chúng ta sẽ chính thức bước vào kì thi cuối cùng của cuộc đời học sinh - kì thi Tốt nghiệp THPT Quốc gia.\n\nDẫu biết rằng mỗi người chúng ta đều sẽ có những con đường riêng, và vẫn chưa biết được đến khi nào cơn dịch bệnh này mới chấm dứt, nhưng vì lần cuối cùng được cầm bút và bước vào một kì thi như một người 'học sinh', hãy cùng cố lên nhé! \n\n\"1 ngọn lửa Trần Chuyên\nvẫn cháy dù đêm đen\ndù mưa thu ẩm ướt\ntựa như than tháng Giêng\n\n7 + 2 đếm ngược\nđồng hồ chạy từng đợt\nánh hồng vẫn truyền đi\n0 bao giờ tắt được\"",
