		message:
			'FLASHMOB 1720\n\n"I\'m not an angel, I\'m not a saint,\nI\'ve been a closed book full of mistakes.\nBut when you\'re broken, when you\'re in pain\n...\nI\'ll walk through fire with you!" \n\n---\nLần cuối đi bên nhau,\ncũng là lần cuối để tinh thần của /một bảy hai không/ được rực cháy mãnh liệt nhất.\n\nMặc cho thật nhiều những khó khăn trong suốt thời gian hoàn thiện bài flashmob, chúng ta cuối cùng đã đứng cạnh nhau, nhảy cùng nhau không ngừng nghỉ như thể hòa chung một nhịp đập: "I\'ll walk through fire with you". \n\nLần cuối ấy dù cũng đã trôi qua mất, chúng ta dù cho có phải thêm chữ "cựu" vào trước danh xưng, thì có những điều sẽ không bao giờ thay đổi.\n\nChẳng hạn như\nkhu B vẫn đứng đó\nchuông vẫn reng đúng 7 giờ\ncó tà áo dài vẫn hối hả khi đồng hồ đến 50\n\nThế nhưng, dù mọi thứ vẫn diễn ra và nhân vật chính không còn là chúng ta nữa, \nthì 1720 vẫn sẽ mãi mãi là anh em\nvà Trần Chuyên\nsẽ mãi mãi là Nhà.\n\n----\n"When the dust starts to settle,\nall we need is a little time\nto keep me yours and keep you mine."',
