			"17:/tuan_cuoi_o_Tran_Chuyen\n“..Vậy là lần cuối đi bên nhau\nCay đắng nhưng không đau…”\n\nTấm áo hôm nay chi chít chữ kí với nhiều lời chúc giờ đã là kỉ niệm.\n\nNhững tiết học căng thẳng giờ chỉ còn đọng lại những lời dặn dò.\n\nLớp học ngày nào giờ chỉ còn là một chiếc bảng tên đong đầy nỗi nhớ trong kí ức.\n…\n\n“Cho dù chẳng biết sẽ đi đâu cũng giống như lần đầu”\n\nCảm xúc buồn là thế nhưng rồi chúng ta sẽ lại bắt đầu một cuộc hành trình mới.\n\nDù phía trước con đường có chông chênh như rồi ta cũng sẽ quen.\n\nNiềm vui từ đó xuất hiện, như cách /một • bảy • hai • không/ tạo ra “lửa trại”.\n…\n\n“Y như một giấc mơ trôi”\n\nLửa trại tuy không có lửa thật nhưng chúng ta có chai nước, đèn flash.\n\nTuy không đầy đủ thành viên khối nhưng chúng ta đã bên nhau, dù tập thể lớn hay nhỏ.\n\nTuy chúng ta khác ngày, khác tháng, khác năm nhưng chúng ta mãi mãi là /một • bảy • hai • không/,\n\nLà học sinh chuyên Trần Đại Nghĩa,\n\nLà thành viên của ngôi nhà “20 Lý Tự Trọng, 53 Nguyễn Du”.\n\n….\n\nThanh loading chạy sắp xong nhưng chai nước trong chai có vẻ chưa đầy?\n\n“Lửa trại” ơi, liệu đây sẽ là “lần cuối” nhỉ?",
