			"https://www.facebook.com/2034783130179068/posts/2747087735615267/",
		message:
			'17:/tuan_cuoi_o_Tran_Chuyen\n“Bồi hồi làm sao đấy\nTrong phút giây này\nVà hát khúc ca cuối cùng”\n\nThứ hai cuối.\n \nBạn có còn nhớ ngày đầu mình gặp nhau?\nMỗi người một sắc màu cá tính, nhưng chúng ta phải cùng sống chung dưới một mái ‘Nhà’. Đó cũng là lúc môi trường học, thầy cô và kiến thức đều mới mẻ, nhìn đâu cũng chỉ thấy toàn xa lạ. Những bài kiểm tra đầu chứa đầy nước mắt, những lần bị mắng buồn như thể muốn tan ra, những lần giận dỗi chẳng thiết nhìn mặt nhau thêm nữa,...\n\nTrước tấm bảng màu lục quen thuộc, dòng chảy của kí ức thật-giả đan xen hiện lên trong niềm cảm xúc vô cùng rối bời...\n\nBởi, \n\ncó những ngày mặt trời đỏ gắt như muốn hối thúc chúng ta đừng vì học quá nhiều mà quên đi thời gian hữu hạn bên nhau,\n\ncó những ngày cãi vã, trời cứ mưa hoài không dứt, còn lòng mình thì mãi chẳng nguôi ngoai một nỗi buồn lam biếc,\n\ncũng chẳng thiếu những ngày trời hanh vàng óng ánh giữa những cơn mưa mùa hạ khiến mình tự hỏi: ở cuối con đường, thực sự có hạnh phúc hay không? \n\nĐột nhiên, \n\ntiếng chuông reo lên, mọi người ra về. Dòng kí ức bị ngưng lại, chỉ còn cảm xúc bối rối trong nỗi lòng.\n\nMới ngày thứ hai lần đầu gặp, thoắt cái đã đến ngày thứ hai của lần cuối học chung một lớp.\n\nNgày thứ hai của những phút giây căng-như-dây-đàn nhưng giờ chỉ còn đọng lại những lời dặn dò và nụ cười chúc may mắn trên con đường mà mình đã chọn. \n\nNgày thứ hai luôn mở đầu với bài thi môn Văn mà bản thân thấy thật khó viết nhưng sao giờ ngồi trước giờ chia tay, lòng mình lại nghẹn, ý văn cứ tuôn ra mãi cho một đề văn mình muốn viết hoài không buông bút, bởi lẽ:\n“Có bao giờ mà ta không buồn đâu, khi mà ta chia xa.”\n(trích “Bài hát khối’)\n…\n\n---\nXin chào các bạn,\n\nChúng mình là BTC "17:". \n\nNhằm giúp bạn được tiếp thêm năng lượng từ những giá trị vật chất và có thêm kinh phí để làm \'kỉ yếu khối\', từ hôm nay, chúng mình sẽ mở đơn để đặt Sticker /kí • ức/ với giá 15k/sheet (hình minh họa trong link order).\n\nTuy chúng không quá hoành tráng nhưng BTC chúng mình mong sticker /kí • ức/ có thể cho bạn sức mạnh để chinh phục những hành trình sắp tới và gợi nhắc bạn về một ‘ngôi Nhà’ mà tập thể /một • bảy • hai • không/ từng ở đó, cùng vui, cùng khóc, cùng cười.\n\nTrân trọng,\nBTC Dự án K12, "17:".\n\n> https://bit.ly/31OHB0x',
