			"17:/khi_toi_18\n\nNGÀY TRẠI CỦA CHÚNG TA\n\nNgày mà tất cả chúng ta tưởng như không có nhưng lại diễn ra một cách vô cùng suôn sẻ,\n\nNgày mà mưa không dám rơi nhiều và ngày hôm sau mưa như trút chỉ để cho cảm xúc của /một • bảy • hai • không/ trọn vẹn,\n\nNgày mà chiếc sân khấu tưởng như khô khan trong mọi sáng thứ hai lại âm thầm chứng kiến biết bao nụ cười, nước mắt rơi,\n\nNgày mà bột màu, nước nhà vệ sinh, kể cả thùng rác cũng trở thành những vật dụng khan hiếm và cần thiết cho những “trận chiến”,\n\nNgày mà bồn rửa tay trở thành bồn gội đầu, ghế ngồi trở thành nơi thắp lên ánh nến của ba năm kỉ niệm và lời hò la trở thành một động lực to lớn cho một tập thể,\n\nNgày mà những mảnh ghép tưởng như cô lẻ nhưng khi ghép lại lại tạo thành một đường link chung dẫn đến thanh xuân của 270 con tim,\n\n’30, 29, 28,... ‘3G lag quá, tui chưa scan được mã QR’...\n\n… 20, 19, 18,... ‘ Thôi, nhìn chung với \ntui nè, dzô dzô 14, 13,...’\n\n...5,\n...4,\n...3,\n...2,\n...1,\n\n/một • bảy • hai • không/ !\n \nTất cả \nnhanh như một giấc mơ \nmà giờ dường như đã kết thúc\n…\n\nNhưng,\n\nTrần Chuyên vẫn sẽ mãi là nhà, dù ngôi nhà ấy đôi lúc khiến tụi mình buồn,\n\nKhung cửa đỏ-lam-vàng-lục vẫn ở đó, dù bao năm nữa có trôi qua, để đợi tụi mình về,\n\nHoa phượng đỏ, đồng phục lam, ánh nắng vàng và chiếc bảng lục vẫn ở trong tim chúng ta như dấu mãi không phai, dù ngày dài tháng rộng năm qua đi,\n\nVà,\n\nỔ đĩa 17: vẫn sẽ ở đây, như một nhân chứng sống chứng kiến quá trình bạn lớn lên,\n\nHãy tự tin và dũng cảm để bước tiếp bạn nhé!\n\n“Mình nói ‘một bảy’\nCác bạn đáp...\n__\n\nBGM: \n> Memories by Pictures of the Floating World\n> 22SUNSET by Darrys\n\nCảm ơn CLB Báo Chí - Truyền Thông THPT Chuyên Trần Đại Nghĩa vì những footage thật đẹp và cảm xúc dành tặng cho /một • bảy • hai • không/.",
