			"https://www.facebook.com/khoi12tdn/photos/a.2741504006173640/2741505379506836/?type=3",
		message:
			'Ngày hôm đó,\nlà ngày nắng hiếm hoi trong chuỗi ngày mưa rả rích\nlà ngày gần 300 con người cùng đứng bên nhau xóa nhòa mọi khoảng cách\nlà ngày của những nụ cười nối tiếp nỗi bồi hồi lưu luyến\nlà ngày mà mình hô "MỘT-BẢY", các bạn đáp "HAI-KHÔNG", mình hô "TRẦN ĐẠI NGHĨA", các bạn đáp "LÀ NHÀ!".\n\nBa năm ở Trần Đại Nghĩa, cuối cùng cũng đã đến lúc khép lại.\n\nBa năm tưởng chừng dài đằng đẵng ấy thế mà vừa nhắm mắt một cái, mở ra thì giấc mộng đẹp ở hai-mươi Lý Tự Trọng hay năm-ba Nguyễn Du đã kết thúc.\n\nBa năm khép lại bằng một lời cảm ơn đầy chân thành, bằng một lời xin lỗi đã ấp ủ bấy lâu, bằng cái ôm thắm thiết dịu dàng và bằng cả những cái nắm tay thật chặt như muốn níu giữ lại ba năm vừa qua.\n\nHãy cùng chúng mình “Ctrl + S” lại những khoảnh khắc đẹp đẽ trong ngày trại vừa qua để những ký ức ấy mãi mãi còn đọng lại trong vùng ký ức nhé!',
