		message:
			"17:/clip_khoi/\n\n> NHẤN NÚT MỞ WINDOWS 1720\n\nĐồng hồ điểm tích tắc, ngày hôm ấy, bạn đã được xem lại Windows 1720 từ những phút khởi đầu. \n\nThời gian trôi từng giây, ngày hôm nay, bạn lại được nhìn thấy hệ thống của chúng ta một cách đầy đủ nhất.\n__\n\nGiữa bầu trời đêm của mùa hạ hôm ấy, mặc cho kết thúc trong niềm hạnh phúc vô cùng, cảm xúc của chúng ta đã gặp phải những khó khăn và trắc trở. \n\nThế nhưng...\n\nNếu không những khó khăn ấy, tụi mình đã không có cơ hội hiểu nhau.\n\nNếu không vì kì nghỉ Tết dài đằng đẵng ấy, chúng mình đã không thể sống chậm lại và yêu thương nhau nhiều hơn. \n\nNếu không có bạn, chiếc clip khối này đã không thể hoàn thiện.\n\nGiờ đây, hãy ngồi lại và chuẩn bị cho những gì sắp được trình chiếu. \n\nVà hãy luôn nhớ: file có thể bị mất đi, màn hình có thể tắt nhưng ổ đĩa 17: sẽ mãi mãi lưu lại tất cả những kí ức này.\n\nNhững khoảnh khắc cuối cùng cùng nhau,\nta Ctrl+S tất cả vào kí ức. \n__\n\nXin được gửi lời cảm ơn chân thành đến bạn Lê Anh Minh CA11720 (Anh Minh Lê)  vì đã góp phần không nhỏ biến chiếc video này thành hiện thực.",
