			"17:/mon_qua_tu_1720/\n\n“Một móc khoá dẫn đến tình thương \nMột huy hiệu trữ đầy kỉ niệm \nGói cà phê nhắc bạn tỉnh táo \nTránh thức khuya rồi lại ngủ quên!\nQuên luôn giờ thi,\nQuên luôn báo thức,\n\nChiếc đồng hồ đếm ngược từng giây!\n\nDẫu mai này chuyện gì xảy đến, \nMột bảy hai không ơi, đừng sợ!\nVòng ngọc trên tay còn đỏ lời chúc phúc\nÁnh lên màu kí ức lúc chia tay,\nCùng chiếc vòng nhạt trao gửi những lời hứa \nRằng vẫn khoác vai nhau, mặc kệ khóc hay cười”.\n__\n\n17:/mon_qua_tu_1720/moc_khoa\n> Móc khoá có hình logo 17: /một • bảy • hai • không/ với QR code dẫn đường bạn tới clip khối bất cứ lúc nào bạn muốn nhớ lại những kí ức trong ổ đĩa 17:\n \n17:/mon_qua_tu_1720/huy_hieu\n> Huy hiệu biểu tượng “Ctrl+S” nhắc bạn lưu giữ những kỉ niệm 3 năm vừa qua trước khi bắt đầu các cuộc hành trình mới.\n \n17:/mon_qua_tu_1720/ca_phe\n> Gói cà phê mong bạn luôn tỉnh táo, vững vàng, sảng khoái, đầy năng lượng để không gục ngã trước sấp đề ôn và bài tập dày cộp.\n\n17:/mon_qua_tu_1720/vong_ngoc_mau_do\n> Một chiếc vòng đỏ với viên ngọc may mắn chúc bạn thi có kết quả tốt thật tốt.\n\n17:/mon_qua_tu_1720/vong_nhat\n> Một chiếc vòng màu vàng/xanh lá/xanh dương của sự gắn bó. \n> Hãy tặng chiếc vòng màu thay lời cảm ơn hoặc xin lỗi đến một người nào đó mà thời gian qua bạn chưa có dịp bày tỏ, hoặc đơn giản, hãy tặng nó cho người đã làm cấp 3 của bạn long lanh, ấm áp như ánh nến ngày trại.",
