			"https://www.facebook.com/2034783130179068/posts/2738694223121285/",
		message:
			'Dù cho có không trọn vẹn, cảm xúc của chúng ta vẫn luôn đong đầy.\n\nBởi lẽ\nchúng ta đã luôn có nhau,\nchúng ta đã cùng hòa làm một,\nvà chúng ta đã không quên Ctrl + S\nđể kịp thời lưu kỷ niệm thành file.\n\n---\nChính nhờ bạn, ổ đĩa 17: hôm nay vừa được cập nhật thêm những tệp tin mới\n\nlà sự gắn kết giữa mấy trăm con người, thứ thường được ca ngợi trong những trang giấy, giờ đã được mắt thấy tai nghe\n\nlà những xúc cảm không mới, nhưng cũng chưa bao giờ là cũ để nói ra, để rồi được đồng cảm và chia sẻ bởi cả một thế hệ\n\nlà sự nhiệt huyết tới tận cùng, để đi cùng nhau như chưa bao giờ được cùng bước, thứ mà dường như bất kì ai trong chúng ta cũng chưa từng thấy suốt một thời tuổi trẻ \n\nlà sự tiếc nuối và hụt hẫng khi thứ bấy lâu mong chờ không cách nào trọn vẹn, nhưng chính những tiếng an ủi và cảm thông đã đủ để những chỗ trống được lấp đầy. \n\n---\nDẫu cho có muộn màng, chúng mình tin rằng giờ đây vẫn kịp để tạo tệp tin mới và bấm Ctrl + S chúng vào ổ đĩa của chúng ta\n\nnơi gửi gắm lời xin lỗi từ tận sâu trong đáy lòng của chúng mình đến với các bạn, vì những thiếu sót và mất mát đã khiến những gì là đẹp nhất không hoàn hảo\n\ncũng là nơi đủ để viết một lời cảm ơn\nvì đã luôn ở cạnh khi chúng mình cần đến, đã dành cho chúng mình tất cả những lời động viên và thấu hiểu chân thành nhất để cõi lòng chúng mình được sưởi ấm trở lại,\nđã cho chúng mình biết những gì được hòa nhịp bởi con tim mới là thứ làm nên sự đầy đủ, để chúng mình tin rằng những gì vừa trôi qua sẽ không cứ thế mà vô nghĩa trôi đi. \n\n---\nCon tim bạn và mình giờ đã chạy hết công suất, hãy ngủ một giấc thật sâu để não bộ được nghỉ ngơi và những gì đẹp đẽ nhất sẽ kịp thời được lưu lại\n\nrằng vào hôm ấy, không có một điều gì tiếc nuối, chỉ có hình ảnh 270 con người, mặc cho trên người lấm lem những mảng màu đã ướt, vẫn nắm tay nhau, đi bên nhau với nụ cười tươi trên môi, miệng không ngừng hô vang:\n\n"một bảy hai không!\nmột bảy hai không!\nmột bảy hai không!\n..."',
