		message:
			'NHỮNG NỐT NHẠC ĐẦU TIÊN\n\nHơn ba tuần trước, một lời kêu gọi đã được âm thầm truyền đến tất cả các lớp trong khối, rằng những con người của thế hệ 17-20 chúng ta hãy cùng nhau viết lên những giai điệu cho chính mình.\n\nHơn ba tuần sau, với tất cả tâm huyết và sự tự hào, những giai điệu được mong đợi ấy cuối cùng cũng đã thành hình, chỉ chờ ngày được tất cả mọi người thưởng thức.\n\nGiờ đây, hãy ngồi xuống, vặn lớn loa và cùng lắng tai nghe những xúc cảm trong lời ca. Bởi sẽ có một ngày, tất cả chúng ta sẽ cùng ngân nga nó,\nmột khúc ca duy nhất, \nkhúc ca chỉ của riêng chúng ta thôi.\n\n"... chắc không cần đâu,\nvì mình đã chẳng bao giờ rời đi."',
