			'MY LỚP MY LOVE \n\nBa năm trôi qua ở Trần Chuyên ắt hẳn để lại trong mỗi người chúng ta những kỷ niệm, những khoảnh khắc dù vui dù buồn cùng với tập thể mà chúng ta gọi là "lớp". Cứ ngỡ rằng những kỷ niệm ấy rồi sẽ phai nhòa theo năm tháng, nhưng bạn ơi,\n\nchuyện lớp mình đã lưu kĩ thành file,\nlo chi love một ngày sẽ phai? \n\nNgay bây giờ, hãy cùng chúng mình một lần nữa khởi động hệ điều hành Windows Trần Chuyên và đi theo đường dẫn\n17:/ my lớp my love/ [lớp của bạn] \nđể xem điều gì đã và đang được lưu giữ lại nhé!\n\n#mylopmylove\n#clickcolove',
