									"/CS/\n\n1. Trong giờ thể dục, con trai lớp mình chơi nhảy dây còn con gái thì chơi bóng rổ.\n\n2. Bạn X lớp mình mọc 3 cọng râu, bạn Y cảm thấy đó là râu dê nên đã nhăm nhe... “bứt” chúng cơ mà Y không thành công . Đột nhiên vào một ngày đẹp trời nọ, X phát hiện: MÌNH ĐÃ BỊ MẤT 1 CỌNG RÂU. Thế là một trận chiến nảy lửa giữa X và Y đã diễn ra chỉ để bứt luôn cả 2 cọng râu còn lại. \n\n3. Các bạn có biết cái giá của việc trốn học buổi chiều là gì không? Là tụi mình đã có một buổi chiều học riêng cùng thầy Lý giữa căn tin vào một buổi chiều buồn tháng 4 nhưng cái kết chúng mình vẫn được 9đ Lý mọi người ạ hihi.\n\n4. Vào những buổi trưa nóng gắt, các bạn biết làm gì để có một buổi học toán tuyệt vời không? Bí quyết chỉ cần một ly nước cam nguyên chất (50% đường 70% đá) thì ngày hôm ấy chúng ta sẽ có một buổi học Toán tuyệt vời.",
