		message:
			"/màu • lam/\n\nBầu trời hôm nay không xanh như cách nó vẫn thường xanh.\n\nTrời nóng hầm, chuyển sang mưa rồi lại trở lạnh đột ngột. Thời tiết thay đổi liên tục như cảm giác khi ta cầm đề bước ra khỏi phòng thi. Có biết bao cảm xúc đan xen, nào là vui khi hay tin mình không còn phải luyện dạng bài ABC mỗi ngày nữa, nào là tiếc hùi hụi khi “mình hiểu đề nhưng đề không hiểu mình”, nào là “ôi, tui viết (tính) thiếu/sai/lạc câu đó rồi”...\n\nBa năm cấp ba tại Trần Chuyên, khoảng thời gian có nhiều xúc cảm tựa như dải màu biến đổi liên tục từ đỏ đến tím, thế nhưng mắt thường chỉ có thể thấy nó là ánh sáng trắng: đó là khoảng thời gian tụi mình từng chìm đắm vào đèn sách cùng với những suy nghĩ đối nghịch: quyết tâm - bỏ cuộc, học tiếp - chuyển môn, ở lại - rời đi... Rồi cả những người bạn học cùng, những người thầy, người cô đã dạy, những lúc mất ăn mất ngủ, đêm thức trắng học bài, những giọt nước mắt đọng trên giọt mực xanh... Tất cả như hiện lên rõ mồn một trước mắt trong sự hối hả khi nộp bài.\n\nMọi chuyện kết thúc rồi nhỉ?\n…\n\nThi xong, môn chuyên này còn có dùng cho việc gì khác? Thi xong, sau này không học lớp chuyên, không học trường chuyên nữa, những kiến thức này này có bị xóa nhòa theo năm tháng? Thi xong, người bạn năm nào cùng chiếc đồng phục xanh ấy, liệu còn bên cạnh khi mình cần nhớ lại những cảm xúc này?\n \nTrước sự hối hả của cuộc sống, mình chỉ muốn ‘Ctrl + S’ sắc xanh của bầu trời hôm ấy, của bộ đồng phục mình đã khoác hôm ấy, của màu xanh thoáng của nỗi thở dài xen lẫn chút buồn và vui hôm ấy để lưu lại từng điều một, để khi nhiều thứ đã trôi vào quên lãng, những điều quan trọng nhất sẽ ở lại…",
