		icon: "https://www.facebook.com/images/icons/photo.gif",
		permalink_url:
			"https://www.facebook.com/2034783130179068/posts/2719222125068495/",
		message:
			"ĐỎ VÀNG LỤC LAM, LIỆU TA ĐÃ TỪNG GẶP GỠ?\n\nCó những khoảng thời gian khiến ta không bao giờ quên, người ta gọi nó là tuổi trẻ.\n\nThế nhưng, giữa khoảng trời vô định với vô vàn sắc màu đó, ta có chắc sẽ nhớ hết tất cả từng gam màu một, rõ nét đến mãi mãi về sau?\n\nNhững gì ta đã quen, đã nhớ và đã yêu không phải lúc nào cũng là thứ ta vẫn quen, vẫn nhớ và vẫn yêu. Bởi vốn là con người, sự vô hạn của trái tim và hữu hạn của bộ não không cho phép tất cả các kí ức cùng đi qua bộ lọc tiềm thức một cách dễ dàng.\n\nChẳng hạn như, \nbạn đã nhìn thấy màu xanh lá trên những cây thông già,\nđã quen màu đỏ thắm của đóa hoa phượng vĩ,\nđã nhớ cả màu dương biếc trên chiếc đồng phục chẳng xa lạ chi,\nhay đã trót tương tư ánh nắng vàng sơn màu hoài niệm.\n\nNhững gam màu này, bạn biết rằng bạn đã thấy chúng, và bạn cũng thừa biết rằng ở đây, chúng luôn tồn tại. Nhưng nếu một ngày, khi bất chợt một ai đó hỏi rằng: màu của Trần Đại Nghĩa là gì? Lúc đó, bạn liệu có nhớ về chúng?\n\n—\n\nTrí não con người ta lắm lúc cũng lạ kỳ, nó ghi nhớ và dễ liên kết những thứ đi cùng nhau hơn là những gì nhỏ lẻ.\n\nĐỏ vàng lục lam, ừ đúng, chúng cũng vậy.\n\nBạn không phải lúc nào cũng nhớ rằng chúng là những sắc màu trải khắp Trần Chuyên, nhưng bạn biết rằng nó chính là bốn sắc thái rực rỡ nhất của những ô cửa sổ kiểu Pháp đã soi chiếu ngần ấy năm ở hội trường A.\n\nBạn không phải lúc nào cũng để ý dưới chân mình không chỉ có màu vàng, nhưng đâu đó trong tiềm thức bạn biết rằng những viên gạch lót và lớp men tráng khắp khu B mà chúng ta đã đi lại suốt ba năm trời, cũng có bốn màu lam lục vàng đỏ.\n\nChúng luôn hiện hữu, và bạn luôn biết.\nNhưng chúng không phải là câu trả lời đầu tiên cho câu hỏi màu của Trần Đại Nghĩa.\nCó lẽ, vì dù có ở khắp nơi, trong mắt của chúng ta, chúng thật nhỏ bé.\n\n—\n\nỞ Trần Đại Nghĩa, có những thứ bạn sẽ nhớ mãi, nhưng cũng có những thứ, những người một ngày nào đó sẽ trôi đi mất mà không nằm trong tầm kiểm soát của bản thân bạn.\n\nMỗi một điều như thế đều chính là một viên gạch để tạo nên đủ bốn màu, dù nhỏ bé nhưng đều từng ngày chứng kiến sự trưởng thành và chuyển hóa của mỗi người chúng ta từ khi còn ban sơ nhất. Nhưng cứ như một quy luật bất biến, tất cả rồi cũng sẽ phai nhạt cho tới khi ta dần quên mất đi.\n\nVậy thì, hãy dùng quy luật để giải trừ quy luật.\n\nNếu đi cùng nhau chính là chìa khóa, vậy hãy cùng nhau đi hết quãng thời gian còn lại, giống như cách những viên gạch đứng cạnh nhau. Hãy bên nhau để không phải hối tiếc những gì đáng lẽ ta đã có thể làm. Hãy bên nhau để nhận ra những gì ta đã quên lãng rồi cùng lưu trữ nó lại vào hệ thống của tiềm thức. Hãy để một lần và mãi mãi không phải lại lãng quên.\n\nKhi tất cả được đặt về cùng một chỗ, đó cũng là lúc những ký ức đã được lưu trữ trọn vẹn vào trí óc và trái tim của mỗi bản thể tựa như những ô cửa sổ lung linh sắc màu, thanh thuần và rực rỡ.\n\n—\n\nWindows chỉ đủ bốn màu khi bạn nhớ rõ:\n“1720 là có một không hai,\nđừng bao giờ quên ‘Ctrl + S’ vì sẽ mất file.”",
