		message:
			'"tụi mình không trở lại, vì đã chẳng bao giờ rời đi."\n\nxin chào, \nvẫn là \'một bảy hai không\' (17:) đây.\n\nkì nghỉ tết tưởng chừng kéo dài mãi mãi cuối cùng đã kết thúc, đủ để cho ai đó sống chậm lại một chút và yêu thương nhiều hơn, và dư để tụi mình có thể cất tiếng ít lại một chút nhưng chuẩn bị nhiều hơn để bù đắp cho những hoạt động bị trì hoãn trong đại dịch.\n\nhơn ai hết, tụi mình rõ rằng hiểu lầm là rất dễ, còn hiểu nhau thì mất rất nhiều thời gian. nhưng sẽ không vì thế mà tụi mình nản lòng hay từ bỏ.\nxin lỗi vì để mọi người đợi lâu, nếu lỡ quên tên nhau thì hãy giới thiệu và làm quen lại từ đầu.\n\n“\nDự án Khối 12 của thế hệ Trần Chuyên 1720 mang tên 17:\nđọc là “một bảy hai không”\nviết là “17:”\n\n< VỀ HÌNH THỨC >\n • 17 có thể tạo thành chữ D\n • Dấu hai chấm được tạo từ hai số không, là phần "hai không" trong "một bảy hai không" tương đương với "20" trong "1720"\n\n< VỀ NỘI DUNG >\n • Dự án 17: giống như ổ đĩa DATA (D:) trên máy tính - là nơi lưu trữ kỉ niệm của năm 17 tuổi tụi mình có nhau \n • Tụi mình là thế hệ thứ 17 của trường khi vào trường năm 2017 và trường được thành lập năm 2000\n\n< VỀ CÁCH KHAI THÁC >\n • Dự án 17: là ổ đĩa, folder là các lớp, file là kỉ niệm của 1720\n • "Ctrl + S" là phím tắt để save các file kỉ niệm của tụi mình vào thư mục các lớp cũng như Dự án 17:\n • Bốn màu của Windows 7 được sử dụng xuyên suốt và gắn liền với các lớp để góp phần thể hiện chủ đề của Dự án, và màu chủ đạo là classic blue (Color of the Year 2020)\n"\n(trích từ bài đăng hỏi ý kiến và giải đáp thắc mắc về avatar và tên dự án gửi đại diện các lớp, ngày 5 tháng 2 năm 2020)\n\nbây giờ thì\ngiới thiệu mode: off,\ntìm hiểu mode: on.\ndự án đã bật đèn xanh, các bạn đừng chần chừ mà hãy vào page chọn “see first” thật nhanh để sau này không còn phải hoang mang khi nghe quá nhiều thông tin chưa xác thực nữa. dù ai nói ngả nói nghiêng, tìm hiểu trực tiếp vẫn là cách tốt nhất để bắt đầu mọi mối quan hệ mà.\n\nlời cuối cùng, \nmong rằng tháng ngày cách ly đã không làm ta cách lòng, và sẽ trở thành chất xúc tác để 1720 càng trân quý thời gian ngắn ngủi còn lại bên nhau.\n\n"Vì 1720 là có một không hai,\nđừng bao giờ quên \'Ctrl + S\' vì sẽ mất file."',
