			"DATA (17:)\n/một-bảy hai-không/\n \nĐã bao giờ bạn tự hỏi, rốt cuộc những kỉ niệm mình cùng vun đắp trong ba năm rồi sẽ đi về đâu khi ‘thanh xuân’... load đầy?\n \nVậy, Dự án Khối 12 năm nay sẽ là ổ đĩa 17: với dung lượng vô tận để lưu trữ tất cả những khoảnh khắc của tuổi mười bảy mình có nhau,\nđã cùng đếm ngược “3, 2, 1” trước biết bao thời khắc quan trọng, \nđã cùng tưởng tượng ngày hội “Ngộ” dù vẫn chưa đến lúc chia xa.\n \nĐể sau này, lỡ có nhớ nhau không chịu được giữa dòng đời bộn bề xuôi ngược, chỉ cần ‘Ctrl + F’  ổ đĩa 17: của chúng ta,\ntìm về những file kỉ niệm quen thuộc từ muôn khoảng trời xa lạ, \ntìm về thế hệ thứ mười bảy của Trần Chuyên rực rỡ tuổi hai mươi.\n \nVì một-bảy hai-không là có một không hai,\nđừng bao giờ quên ‘Ctrl + S’ vì sẽ mất file.",
